.jq-toast-wrap {
  margin: 0;
  padding: 0;

  * {
    margin: 0;
    padding: 0; }

  display: block;
  position: fixed;
  width: auto;
  pointer-events: none !important;
  letter-spacing: normal;
  z-index: 9000 !important;

  &.bottom-left {
    bottom: 20px;
    left: 20px; }

  &.bottom-right {
    bottom: 20px;
    right: 40px; }

  &.top-left {
    top: 20px;
    left: 20px; }

  &.top-right {
    top: 20px;
    right: 40px; } }

.jq-toast-single {
  display: block;
  width: 100%;
  padding: 10px 20px 10px 10px;
  margin: 0 0 5px;
  border-radius: 4px;
  font-size: 16px;
  font-family: arial,sans-serif;
  line-height: 22px;
  position: relative;
  pointer-events: all !important;
  background-color: #444;
  color: #fff;

  h2 {
    font-family: arial,sans-serif;
    font-size: 14px;
    margin: 0 0 7px;
    background: 0 0;
    color: inherit;
    line-height: inherit;
    letter-spacing: normal; }

  a {
    color: #eee;
    text-decoration: underline; }

  ul {
    margin: 0 0 0 15px;
    background: 0 0;
    padding: 0;

    li {
      list-style-type: disc !important;
      line-height: 17px;
      background: 0 0;
      margin: 0;
      padding: 0;
      letter-spacing: normal; } } }

.close-jq-toast-single {
  position: absolute;
  top: 3px;
  right: 7px;
  font-size: 14px;
  cursor: pointer; }

.jq-toast-loader {
  display: block;
  position: absolute;
  top: -2px;
  height: 5px;
  width: 0;
  left: 0;
  border-radius: 5px;
  background: red; }

.jq-toast-loaded {
  width: 100%; }

.jq-has-icon {
  padding: 10px 10px 10px 50px;
  background-repeat: no-repeat;
  background-position: 10px; }

.jq-icon-info {
  background-image: url(data:image/png;base64,);
  background-color: #31708f;
  color: #d9edf7;
  border-color: #bce8f1; }

.jq-icon-warning {
  background-image: url(data:image/png;base64,);
  background-color: #8a6d3b;
  color: #fcf8e3;
  border-color: #faebcc; }

.jq-icon-error {
  background-image: url(data:image/png;base64,);
  background-color: #a94442;
  color: #f2dede;
  border-color: #ebccd1; }

.jq-icon-success {
  background-image: url(data:image/png;base64,);
  color: #dff0d8;
  background-color: #3c763d;
  border-color: #d6e9c6; }
