@import "vars";

.soon {
	//padding-bottom: 184px
	padding-bottom: 40px;
	position: relative;
	&-slider {
		margin: 0 -12px;
		z-index: 20;
		&.slick-dotted.slick-slider {
			margin: 0 -12px; }
		.slick-arrow {
			top: 44%; }
		.slick-dots {
 } }			//bottom: -137px
	.post {
		display: flex;
		padding-bottom: 40px;
		&-wrap {
			//margin-bottom: 50px
			width: 100%;
			&:hover {
 } } }				//margin-bottom: 4px
	.center-btn {
		margin-top: 14px; }
	&-bg {
		position: absolute;
		bottom: 0;
		right: -310px;
		transform: translate(0%,50%);
		z-index: -1; }
	&.soon-second {
		padding-bottom: 80px; }
	&--indent {
		margin-top: 120px;

		@media screen and (min-width: 768px) and (max-width: 1023px) {
			margin-top: 80px; }
		@media screen and (max-width: 767px) {
			margin-top: 60px; } }
	&__popular {
		position: static;
		padding: 0;
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		ul {
			height: auto;
			margin-top: 12px; }
		.popular-btn {
			position: static;
			min-width: 48px; } } }

@media only screen and (max-width : 900px) {
	.soon {
		&-bg {
			display: none; } } }

@media only screen and (max-width : 768px) {
	.soon {
		padding-bottom: 64px;
		.post-wrap {
			margin: 0;
			&:hover {
				margin-bottom: 0; } }
		&-slider {
			&.slick-dotted.slick-slider {
				padding-bottom: 44px; }
			.slick-dots {
				bottom: 40px; } }
		.center-btn {
			margin-top: 0; } } }

@media only screen and (max-width : 600px) {
	.soon {
		padding-bottom: 48px;
		&-slider {
			&.slick-dotted.slick-slider {
				padding-bottom: 42px; }
			.slick-dots {
				bottom: 20px; } }
		.post {
			max-width: 100%;
			padding: 0 12px 50px; } } }
