.new-main {
    position: relative;
    z-index: 1;
    padding-top: 22px;
    &::before {
        content: '';
        position: absolute;
        top: -220px;
        left: calc(50% - 298px);
        z-index: -1;
        transform: translate(-50%, 0);
        display: block;
        width: 974px;
        height: 783px;
        background-image: url(../img/new-main-bg.svg);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center center;
    }
    
    @media screen and (min-width: 768px) and (max-width: 1023px) {
        &::before {
            top: -190px;
            left: calc(50% - 70px);
        }
    }
    @media screen and (max-width: 767px) {
        &::before {
            top: -100px;
            left: calc(50% - 80px);
            width: 628px;
            height: 505px;
        }
    }

    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: #fff;

        @media screen and (max-width: 1023px) {
            flex-direction: column;
        }
    }
    &__body {
        max-width: 710px;
        width: 100%;
    }
    &__heading {
        font-size: 36px;
        font-weight: 600;

        @media screen and (max-width: 767px) {
            font-size: 20px;
            line-height: 1.4;
        }
    }
    &__description {
        margin-top: 32px;
        font-size: 20px;
        font-weight: 600;

        @media screen and (max-width: 767px) {
            margin-top: 20px;
            font-size: 16px;
        }
    }
    &__btn {
        margin-top: 50px;
        
        @media screen and (max-width: 767px) {
            margin-top: 30px;
        }
    }
    &__img {
        display: block;
        overflow: hidden;
        margin-right: -205px;
        min-width: 641px;
        width: 641px;
        height: 418px;
        border: 10px solid #5EB5FE;
        border-radius: 33px;
        img {
            display: block;
            width: 100%;
            height: 100%!important;
            object-fit: cover;
        }

        @media screen and (max-width: 1699px) {
            margin-right: calc((50% - 50vw) - 95px);
        }
        @media screen and (max-width: 1023px) {
            margin: 30px auto 0;
        }
        @media screen and (max-width: 767px) {
            min-width: 486px;
            width: 486px;
            height: 317px;
        }
    }
}