@import "fonts";
@import "libs";
@import "vars";

*::-webkit-input-placeholder {
	color: $colorGray; }

*:-moz-placeholder {
	color: $colorGray; }

*::-moz-placeholder {
	color: $colorGray; }

*:-ms-input-placeholder {
	color: $colorGray; }

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	outline: none;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

a, a:hover, a:focus, a:active, a:visited {
	text-decoration: none;
	transition: $transition; }

li {
	list-style: none; }

html {
	overflow-x: hidden; }

body {
	font-family: $Rubik;
	font-size: 16px;
	line-height: 1.3;
	color: $colorMain;
	background: #F2F5FB;
	overflow-x: hidden;
	text-rendering: optimizeLegibility;
	-webkit-font-smoothing: antialiased;
	overflow: hidden; }

img {
	max-width: 100%;
	height: auto!important; }

button,select,input,textarea {
	font-family: $Rubik; }

button {
	cursor: pointer;
	transition: $transition;
	border: none;
	background: transparent;
	appearance: none; }

input,textarea {
	appearance: none;
	box-shadow: none; }

.wrapper {
	max-width: 1296px;
	margin: 0 auto;
	padding: 0 36px; }

.forgot {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: space-between;
	padding: 3px 0 40px;
	&-link {
		font-size: 14px;
		line-height: 25px;
		color: $colorGray;
		&:hover {
			color: $colorAccent; } } }

.warnings {
	background: #FFF8E2;
	border-radius: $borderRadius;
	padding: 24px 24px 24px 72px;
	position: relative;
	max-width: 600px;
	margin: 0 auto 40px;
	svg {
		position: absolute;
		top: 24px;
		left: 24px; }
	p {
		font-size: 14px;
		line-height: 24px;
		color: #9F9A8A;
		a {
			color: $colorAccent; } } }

.br {
	height: 1px;
	background: #DDE1E9; }

