.ui-loader {
	display: none; }

.section {
	min-height: calc(100vh - 606px); }

.text-anim {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	font-size: 16px;
	line-height: 20px;
	color: $colorGray;
	svg {
		margin-left: 16px; } }

.text-green {
	color: #65C58C!important; }
.text-purple {
	color: #7E57C2!important; }
.text-red {
	color: #EF5350!important; }
.text-blue {
	color: #26C6DA!important; }
.text-yellow {
	color: #CDDC39!important; }
.text-brown {
	color: #8D6E63!important; }
.text-gray {
	color: #78909C!important; }
.text-orange {
	color: #FF7043!important; }
.text-graylight {
	color: #BDBDBD!important; }
.text-orange-2 {
	color: #F2994A!important; }
.text-red-2 {
	color: #FF655B!important; }
.text-blue-2 {
	color: $colorAccent!important; }
.text-stopped {
	color: #c7ce8e !important; }

.question {
	font-size: 14px;
	line-height: 20px;
	color: $colorMain;
	position: relative;
	z-index: 10; }
.cabinet-control__row {
	.creation {
		ul {
			width: 100%!important; } } }
.main-list {
	.creation {
		ul {
			width: 100%;
			@media only screen and (max-width : 450px) {
				text-align: center; } } } }
.creation {
	position: relative;
	z-index: 20;
	ul {
		width: 100%; }
	&:hover {
		ul {
			opacity: 1;
			visibility: visible; } } }
.header-top {
	.creation {
		z-index: 10; } }
.question,
.creation {
	&-toltip {
		display: inline-block;
		width: 24px;
		height: 24px;
		background: #E6E9F1;
		border-radius: 50%;
		text-align: center;
		font-size: 12px;
		line-height: 24px;
		font-weight: normal;
		color: $colorMain;
		margin-left: 6px;
		cursor: pointer;
		position: relative;
		&:hover {
			ul {
				opacity: 1;
				visibility: visible; } } }
	ul {
		position: absolute;
		z-index: 5;
		top: 100%;
		left: 0;
		width: 168px;
		background: #fff;
		box-shadow: 0px 10px 25px rgba(150, 162, 183, 0.3);
		border-radius: $borderRadius;
		overflow: hidden;
		margin-top: 8px;
		text-align: left;
		transition: $transition;
		opacity: 0;
		visibility: hidden;
		li {
			a {
				display: block;
				padding: 14px 24px;
				font-size: 14px;
				line-height: 20px;
				color: $colorMain;
				&:hover,&.active {
					background: #F7FAFF;
					color: $colorAccent; } } } } }
.tab {
	display: none;
	&.tab-active {
		display: block; } }

.prompt {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	z-index: 20;
	li {
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		font-size: 20px;
		line-height: 1.5;
		color: $colorGray;
		cursor: pointer;
		a {
			display: flex;
			flex-flow: row nowrap;
			align-items: center;
			color: $colorGray; }
		span {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 24px;
			height: 24px;
			border: 1px solid #CCD3DC;
			border-radius: 50%;
			margin-right: 12px;
			position: relative;
			transition: $transition;
			i {
				position: absolute;
				top: -16px;
				left: 50%;
				transform: translate(-50%,-100%);
				display: block;
				width: 170px;
				text-align: center;
				background: $colorGray;
				border-radius: $borderRadius;
				box-shadow: 0px 5px 20px rgba(150, 162, 183, 0.25);
				font-size: 11px;
				line-height: 15px;
				font-style: normal;
				color: #fff;
				padding: 5px 8px;
				opacity: 0;
				visibility: hidden;
				transition: $transition;
				&::before,&::after {
					content: '';
					position: absolute;
					bottom: -5px;
					left: 50%;
					border-top: 5px solid $colorGray;
					border-right: 5px solid transparent;
					border-left: 5px solid transparent;
					margin-left: -5px; } } }
		b {
			font-weight: normal; }
		&:hover {
			span {
				border-color: #DDE1E9;
				background: #DDE1E9;
				i {
					opacity: 1;
					visibility: visible; } } } } }

@media only screen and (max-width : 1020px) {
	body {
		padding-top: 136px; } }

@media only screen and (max-width : 768px) {
	.warnings {
		max-width: 100%;
		margin-bottom: 32px; }
	.mobile-align-center {
		text-align: center; } }

@media only screen and (max-width : 700px) {
	body {
		padding-top: 97px; }
	.wrapper {
		padding: 0 20px; } }

@media only screen and (max-width : 450px) {
	.forgot {
		padding-bottom: 30px; }
	.warnings {
		margin-bottom: 24px; } }

@import "_media";
@import "_custom";
