/* Buttons
--------------------------------------------------------------------------------------------------
Lots taken from Flatly (MIT): https://bootswatch.com/4/flatly/bootstrap.css
*/
/* reset */
.fc-button {
  border-radius: 0;
  overflow: visible;
  text-transform: none;
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit; }

.fc-button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color; }

.fc-button {
  -webkit-appearance: button; }

.fc-button:not(:disabled) {
  cursor: pointer; }

.fc-button::-moz-focus-inner {
  padding: 0;
  border-style: none; }

/* theme */
.fc-button {
  display: inline-block;
