.hidden {
	display: none !important; }
.note-editor.note-frame .note-toolbar {
	text-align: left; }
.disabled {
	opacity: .5;
	pointer-events: none !important;
	&-gray {
		opacity: .5;
		pointer-events: none !important;
		background-color: silver;
		color: #FFFFFF; } }
.disabled-light {
	opacity: .5;
	&-gray {
		background-color: silver;
		color: #FFFFFF;
		&:hover {
			background-color: silver; } } }
li.has-inp {
	pointer-events: none;
	color: silver;
	background: rgba(192, 192, 192, 0.1); }
a.delete-test-task {
	color: silver;
	font-size: 28px;
	padding: 0 8px;
	margin-left: -36px;
	margin-top: -4px;
	line-height: inherit;
	display: inline-block;
	vertical-align: middle; }

a.delete-test-variant {
	color: silver;
	font-size: 24px;
	padding: 4px 8px;
	margin-left: -34px;
	margin-right: 2px; }

a.delete-test-task,
a.delete-test-variant {
	&:hover {
		color: darkred; } }

.loader {
	border: 4px solid #ECECEC;
	animation: spin .8s linear infinite;
	border-top: 4px solid #5c9dd4;
	border-radius: 50%;
	width: 50px;
	height: 50px;
	margin: 10px auto;
	&.absolute {
		position: absolute;
		z-index: 1;
		left: 50%;
		top: 30%; }
	&.to-bottom {
		position: absolute;
		z-index: 1;
		left: 50%;
		bottom: 0;
		width: 42px;
		height: 42px;
		margin: 8px auto; } }

.header-bell {
	.loader {
		animation: none; }
	&.drop-active {
		.loader {
			animation: spin .8s linear infinite; } } }


@keyframes spin {
	0% {
		transform: rotate(0deg); }
	100% {
		transform: rotate(360deg); } }

div.users-table {
	position: relative; }

.mr-10 {
	margin-right: 10px!important; }
.pb-20 {
	padding-bottom: 20px!important; }

div.mt-40-b {
	margin-top: 40px;
	border-top: 1px solid #E3E6EF;
	padding-top: 40px; }

li.permission {
	white-space: nowrap;
	line-height: 1.2em; }
.tariff {
	.checkbox.checkbox-big {
		input[type="checkbox"] {
			z-index: auto; }
		span {
			cursor: pointer; } } }

.step-filling__row:not(.step-active) {
	cursor: pointer;
	&:hover {
		border: 2px solid rgba(94, 181, 254, 0.2);
		transition: 0.3s; }
	&.step-order-row {
		border: 2px solid rgba(192, 192, 192, 0.2);
		transition: 0.3s;
		&:hover {
			border: 2px solid rgba(94, 181, 254, 0.8);
			transition: 0.3s; } }
	&.step-disabled {
		cursor: no-drop!important;
		opacity: 0.8;
		border: 2px solid rgba(192, 192, 192, 0.2);
		transition: 0.3s; } }
