@media only screen and (max-width : 600px) {
	.marathon {
		&-block {
			display: block;
			height: auto;
			&__row {
				display: block; }
			&__img {
				padding: 0; }
			&__info {
				padding: 16px 0 24px; }
			&__ball {
				top: 24px;
				right: 24px;
				transform: translate(0,0); }
			&__drop {
				left: auto;
				right: -24px;
				width: 280px;
				padding: 20px 20px 10px;
				ul {
					li {
						padding-bottom: 10px;
						font-size: 12px;
						line-height: 20px; } } }
			.estimate {
				justify-content: center; } }
		&-description {
			&__wrap {
				//padding: 32px 0 40px
				padding: 32px 0 0; } }
		&-slider {
			&.slick-dotted {
				padding-bottom: 24px;
				margin-bottom: 32px;
				width: calc(100% + 16px);
				.slick-dots {
					li {
						width: 100%;
						max-width: 40px;
						margin: 0 8px; } } } }
		&-task {
			display: block;
			&__info {
				padding: 4px 0 0; } } }
	.raffle {
		display: block;
		padding: 24px;
		margin: 48px 0 0;
		&-info {
			padding: 0 0 24px; }
		&-title {
			padding-left: 22px; }
		.main-btn,.second-btn {
			display: block; } } }

@media only screen and (max-width : 450px) {
	.marathon {
		.timeline {
			.reviews {
				&-top {
					display: flex;
					padding-bottom: 20px;
					.table-btn {
						top: 43px; } }
				&-name {
					padding: 0; } } }
		&-main {
			.page-title {
				padding-bottom: 24px; }
			&__row {
				margin-bottom: 24px;
				.main-btn {
					margin-bottom: 16px; }
				.first_block {
					justify-content: space-between;
					width: 100%; }
				.second_block {
					.main-btn {
						max-width: 100%;
						width: 100%;
						margin: 10px auto 0; } } } }
		&-category {
			margin-bottom: 12px; }
		&-list {
			min-height: 22px;
			max-width: 100%;
			margin: 0 0 24px;
			justify-content: end;
			li {
				padding: 0 16px 0 0;
				span {
					padding-left: 8px; } } }
		&-img {
			margin-bottom: 24px; }
		&-ul {
			display: block;
			padding-top: 32px;
			p {
				font-size: 16px;
				padding: 0 0 16px; } }
		&-task {
			&__date {
				span {
					font-size: 10px;
					line-height: 1.6;
					font-weight: 500;
					color: $colorGray;
					padding: 0 5px 0 0; }
				p {
					b {
						display: inline; } } }
			&__step {
				font-size: 16px;
				line-height: 30px; } }
		&-step {
			padding: 0 0 24px;
			&__btns {
				padding: 24px 0 0;
				margin: 0 -4px;
				.second-btn {
					min-width: auto;
					margin: 0 4px;
					padding: 9px 10px;
					font-size: 14px;
					svg {
						height: 16px;
						width: 10px;
						margin-right: 10px; }
					&:last-child svg {
						margin: 0 0 0 10px; } } } }
		&-select {
			padding-bottom: 14px;
			.select {
				&-text {
					font-size: 20px;
					line-height: 1.6;
					padding-right: 40px; } } }
		&.marathon-all {
			.page-title {
				padding-bottom: 24px; } } } }
.step-nav {
	justify-content: end; }
.block-bottom {
	display: flex;
	align-items: center;
	padding-bottom: 60px;
	@media only screen and (max-width : 768px) {
		padding-bottom: 0;
		//flex-direction: column
		.marathon-step__btns {
			a {
				min-width: 50px;
				min-height: 50px;
				span {
					display: none; } } }
		.checkbox {
			max-width: 150px; } }
	.checkbox {
		padding: 13px 24px;
		max-width: 180px;
		margin-right: 10px; }
	.second-btn {
		display: flex!important;
		line-height: inherit; }
	.marathon-step__btns {
		padding: 0; } }
.badge-mini {
	display: inline-block;
	padding: 0px 12px;
	font-size: 12px;
	line-height: 24px;
	color: #96a2b7;
	background: #e6e9f1;
	-webkit-border-radius: 12px;
	border-radius: 12px;
	text-align: center;
	&-white {
		background: #ffffff; } }


.events_block {
	display: flex;
	align-items: start;
	justify-content: space-between;
	&_item {
		width: 50%;
		@media only screen and (max-width : 768px) {
			width: 100%; }
		&:last-child {
			margin-left: 40px;
			@media only screen and (max-width : 768px) {
				margin-top: 20px;
				margin-left: 0; } } }
	.event-name-empty {
		display: flex;
		align-items: center;
		flex-direction: row;
		height: 100%;
		justify-content: start;
		img {
			display: none;
			width: 100%;
			margin-right: 10px;
			max-width: 80px; } }
	.soon_events {
		font-weight: 500;
		margin: 15px 0 5px;
		display: block; }
	@media only screen and (max-width : 768px) {
		flex-direction: column; } }
fieldset {
	border: 0; }

/*  Events display */
.events-container {
	overflow-y: scroll;
	height: 100%; }

.events-container:after {
	clear: both; }

.event-card {
	position: relative;
	padding-left: 10px;
	margin-bottom: 15px;
	border-bottom: 1px solid #e3e6ef;
	padding-bottom: 10px;
	&:last-child {
		border-bottom: 0; } }

.event-count, .event-name, .event-cancelled {
	display: inline; }
.event-time {
	color: #96a2b7; }
.event-url {
	&.stream-event {
		&:before {
			content: '';
			background-image: url('/img/icos/stream.svg');
			width: 18px;
			height: 12px;
			display: inline-block;
			margin-right: 5px; } } }


.calendar-container {
	width: 100%;
	position: relative;
	margin: 0px auto;
	background: #fff;
	display: inline-block;
	border: 1px solid #e3e6ef;
	border-radius: 6px;
	&:after {
		clear: both; } }

.event-color {
	content: "";
	width: 5px;
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0; }

.calendar {
	width: 100%;
	display: table;
	padding: 10px 10px 0;
	.select-text {
		border-bottom: 1px solid #e3e6ef;
		border-radius: 0;
		padding: 14px; }
	.select-info {
		margin-top: 0;
		&.nano {
			min-height: 180px; } } }


.year-header {
	background: #f2f7ff;
	text-align: center;
	position: relative;
	padding: 10px 0;
	span {
		display: inline-block; } }

.left-button, .right-button {
	cursor: pointer;
	width: 28px;
	text-align: center;
	position: absolute; }

.left-button {
	left: 0;
	-webkit-border-top-left-radius: 5px;
	-moz-border-radius-topleft: 5px;
	border-top-left-radius: 5px; }

.right-button {
	right: 0;
	-webkit-border-top-right-radius: 5px;
	-moz-border-radius-topright: 5px;
	border-top-right-radius: 5px; }

.days-table, .dates-table, .months-table {
	border-collapse: separate;
	text-align: center;
	width: 100%; }

.days-table,.dates-table {
	width: 100%;
	border-collapse: collapse; }
.dates-table {
	td {
		border: 1px solid #e3e6ef; } }

.day {
	height: 26px;
	width: 26px;
	font-size: 11px;
	line-height: 26px;
	border: 2px solid transparent;
	text-transform: uppercase; }


.month {
	cursor: default;
	height: 26px;
	width: 26px;
	padding: 0 2px;
	padding-top: 10px;
	line-height: 26px;
	text-transform: uppercase;
	font-size: 11px;
	color: #9e9e9e;
	transition: all 250ms;
	&:hover {
		color: #435ebe; } }

.active-month {
	font-weight: bold;
	color: #435ebe; }



.table-date, .day {
	&:hover {
		cursor: pointer; }
	span {
		height: 36px;
		width: 36px;
		//font-size: 12px
		line-height: 26px;
		text-align: center;
		border-radius: 50%;
		border: 2px solid transparent;
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 0 auto; } }

.table-date {
	cursor: default;
	color: #2b2b2b;
	transition: all 250ms; }

.table-date {
	&:not(.event-date) {
		&:hover {
			background: #f2f7ff; } }
	&.nil {
		&:hover {
			background: transparent;
			cursor: default!important; } } }
.active-date {
	border-color: #fff8e2;
	background: #fff8e2; }
.event-date {
	background: #52A0FD;
	color: #ffffff;
	&.active-date {
		background: #65c58c; } }
.old-date {
	background: #f8f6f6;
	color: #cacdd6;
	&.event-date {
		background: rgba(82, 160, 253, 0.15); } }

.cke_maximized {
	body {
		background: #cccccc; } }
